.main-nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  padding-top: 30px;
  padding-bottom: 30px;

  & .logo {
    display: block;
    min-width: 300px;
    min-height: 200px;
    text-align: center;
  }

  & .nav-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    min-height: 90px;
    padding-left: 20px;
    padding-right: 20px;
  }

  & .nav-item {
    height: 100%;

    & .link {
      display: block;
      height: 100%;
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }
}

